import React from 'react';
import { Post } from 'models/post';
import { Box, Button, Grid, makeStyles, Paper, Theme, Typography } from '@material-ui/core';
import { Link } from 'gatsby';
import Image from 'gatsby-image';
import * as Icon from 'react-feather';

const useItemStyles = makeStyles<Theme>(({ palette, spacing, breakpoints }) => ({
  root: {
    position: 'relative',
    overflow: 'hidden',
    '&:hover': {
      '& .gatsby-image-wrapper': {
        transform: 'scale(1.1)',
      },
    },
    '& .gatsby-image-wrapper': {
      transition: 'transform 0.2s ease-in-out',
    },
  },
  image: {
    height: 150,
    [breakpoints.up('lg')]: {
      height: 300,
    },
  },
  dimmed: {
    position: 'absolute',
    width: '100%',
    height: '100%',
    backgroundColor: `rgba(0, 0, 0, 0.5)`,
    top: 0,
    left: 0,
    padding: spacing(1),
  },
  title: {
    color: palette.common.white,
    textAlign: 'center',
  },
  viewCount: {
    color: palette.common.white,
  },
}));

const PostItem = ({ post }: { post: Post }) => {
  const styles = useItemStyles();

  return (
    <Paper className={styles.root} elevation={0}>
      <Image className={styles.image} fluid={post.frontmatter.thumbnail.childImageSharp.fluid} />
      <Box className={styles.dimmed} display="flex" flexDirection="column" alignItems="center" justifyContent="center">
        <Typography className={styles.title}>{post.frontmatter.title}</Typography>
      </Box>
    </Paper>
  );
};

const useSectionStyles = makeStyles<Theme>(({ spacing, typography, palette, breakpoints }) => ({
  root: {
    marginBottom: spacing(5),
  },
  title: {
    fontWeight: typography.fontWeightBold,
  },
  viewMoreLink: {
    color: palette.primary.main,
    textDecoration: 'none',
    display: 'flex',
    alignItems: 'center',
    [breakpoints.down('md')]: {
      display: 'none',
    },
  },
  viewMoreMobileButton: {
    marginTop: spacing(2),
    [breakpoints.up('lg')]: {
      display: 'none',
    },
    '& a': {
      textDecoration: 'none',
    },
  },
}));
interface Props {
  title: string;
  posts: Post[];
  viewMoreLink?: string;
  onClickPost?: (path: string) => void;
  onClickViewMore?: () => void;
}
const ContentsSection = ({ title, posts = [], viewMoreLink, onClickPost, onClickViewMore }: Props) => {
  const styles = useSectionStyles();
  return (
    <section className={styles.root}>
      <Box display="flex" justifyContent="space-between" alignItems="center">
        <Typography className={styles.title} component="h3" variant="h6" gutterBottom>
          {title}
        </Typography>
        {viewMoreLink && (
          <Link className={styles.viewMoreLink} to={viewMoreLink} onClick={onClickViewMore}>
            Îçî Î≥¥Í∏∞ <Icon.ChevronRight />
          </Link>
        )}
      </Box>
      <Grid container spacing={1}>
        {posts.map(p => (
          <Grid key={p.fields.slug} item xs={12} md={3}>
            <Link to={p.fields.path} onClick={() => onClickPost?.(p.fields.path)}>
              <PostItem post={p} />
            </Link>
          </Grid>
        ))}
      </Grid>
      {viewMoreLink && (
        <Grid className={styles.viewMoreMobileButton} container>
          <Grid item xs={12}>
            <Link to={viewMoreLink}>
              <Button fullWidth={true} variant="outlined" onClick={onClickViewMore}>
                Îçî Î≥¥Îü¨Í∞ÄÍ∏∞
              </Button>
            </Link>
          </Grid>
        </Grid>
      )}
    </section>
  );
};

export default ContentsSection;
