import React, { useEffect, useMemo } from 'react';
import Layout from 'components/Layout';
import SEO from 'components/SEO';
import { generateLogger } from 'utils/loggers';
import { usePopularPosts } from 'hooks/usePopularPosts';
import { useAllPosts } from 'hooks/useAllPosts';
import { Post } from 'models/post';
import Jumbotron from './Jumbotron';
import ContentsSection from './ContentsSection';
import CategorySection from './CategorySections';

const logger = generateLogger('home_page');
const handlePostClick = (path: string) => {
  logger.click('click_posting', { path });
};
const handleViewMoreClick = (title: string) => {
  return () =>
    logger.click('click_view_more', {
      title,
    });
};
const handleCategoryClick = (categoryName: string) => {
  logger.click('click_category', { categoryName });
};

const filterCategory = (posts: Post[], category: string, limit: number = posts.length - 1) => {
  return posts
    .filter(p => {
      return p.frontmatter.categories?.includes(category) && p.frontmatter.mainSection === category;
    })
    .slice(0, limit);
};

const HomePage = () => {
  const allPosts = useAllPosts();
  const popularPosts = usePopularPosts().slice(0, 4);

  const essays = useMemo(() => filterCategory(allPosts, 'ÏóêÏÑ∏Ïù¥', 4), [allPosts]);
  const programming = useMemo(() => filterCategory(allPosts, 'ÌîÑÎ°úÍ∑∏ÎûòÎ∞ç', 4), [allPosts]);

  useEffect(() => {
    logger.view();
  }, []);

  return (
    <Layout>
      <SEO />
      <Jumbotron />
      <CategorySection onClick={handleCategoryClick} />
      <ContentsSection
        title="ÎßéÏù¥ ÏùΩÏùÄ Ìè¨Ïä§ÌåÖÏù¥ÏóêÏöî üèÜ"
        posts={popularPosts}
        onClickPost={handlePostClick}
        viewMoreLink="/posts"
      />
      <ContentsSection
        title="ÏóêÏÑ∏Ïù¥ üìì"
        posts={essays}
        viewMoreLink="/categories/ÏóêÏÑ∏Ïù¥"
        onClickViewMore={handleViewMoreClick('ÏóêÏÑ∏Ïù¥')}
      />
      <ContentsSection
        title="ÍøÄÌåÅ ÎåÄÎ∞©Ï∂ú üçØ"
        posts={programming}
        viewMoreLink="/categories/ÌîÑÎ°úÍ∑∏ÎûòÎ∞ç"
        onClickViewMore={handleViewMoreClick('ÍøÄÌåÅ ÎåÄÎ∞©Ï∂ú')}
      />
      {/* Ï†ÑÏ≤¥ Ïπ¥ÌÖåÍ≥†Î¶¨? */}
    </Layout>
  );
};

export default HomePage;
