import React from 'react';
import { useAllCategories } from 'hooks/useAllCategories';
import { Box, Chip, makeStyles, Theme } from '@material-ui/core';
import { Link } from 'gatsby';
import { usePageRouteUtils } from 'src/hooks/usePageRouteUtils';

const useStyles = makeStyles<Theme>(({ spacing }) => ({
  root: {
    margin: `0 0 ${spacing(5)}px`,
    padding: 0,
    whiteSpace: 'nowrap',
    overflow: 'scroll',
  },
  chip: {
    marginRight: spacing(1),
    marginBottom: spacing(0.5),
    lineHeight: 1.1,
    textDecoration: 'none',
  },
}));

interface Props {
  onClick?: (categoryName: string) => void;
}
const CategorySection = ({ onClick }: Props) => {
  const styles = useStyles();
  const categories = useAllCategories();
  const { getCategoryPath } = usePageRouteUtils();

  return (
    <Box component="section" display="flex" justifyContent="center">
      <ul className={styles.root}>
        {categories.map(({ fieldValue }) => (
          <Link
            className={styles.chip}
            key={fieldValue}
            to={getCategoryPath(fieldValue)}
            onClick={() => onClick?.(fieldValue)}
          >
            <Chip size="small" label={fieldValue} variant="outlined" clickable={true} />
          </Link>
        ))}
      </ul>
    </Box>
  );
};

export default CategorySection;
